@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  margin: 0;
  padding: 0;
}

head {
  font-family: 'Poppins';
}

body {
  font-family: 'Poppins';
}

.divbody{
  min-height: 100vh;
}

.crud-modal-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
}
.crud-modal-wrapper__background {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1000;
}

.crud-modal-wrapper__modal {
  background-color: white;
  border-radius: 5px;
  z-index: 1001;
  position: relative;
  padding: 1rem;
  margin: 3rem auto 0 auto;
  top: 1rem;
  display: inline-block;
}

.crud-modal-wrapper__title {
  border-bottom: 1px solid #e8e8e8;
  padding-bottom: 0.8rem;
  font-size: 1.3rem;
}

.crud-modal-form {
  padding: 1rem;
}

.crud-modal-form__field-error {
  color: #fff;
  background-color: #e81b1b;
  border-radius: 3px;
  padding: 0.3rem 0.8rem;
  text-align: left;
  margin-bottom: 0.5rem;
  position: relative;
  margin-top: -0.6rem;
}

.crud-modal-form__field-error:before {
  content: "";
  background-color: transparent;
  display: block;
  position: absolute;
  top: -0.3rem;
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 6px solid #e81b1b;
}

.crud-modal-form__message {
  padding: 0 0 1.2rem 0;
  font-size: 1.1rem;
  text-align: left;
}

.crud-modal-form button {
  margin-top: 0.5rem;
  min-width: 250px;
  font-family: 'Poppins';
}

.crud-modal-form input,
.crud-modal-form select,
.crud-modal-form textarea {
  border-radius: 5px;
  height: 30px;
  border: 1px solid #ccc;
  min-width: 250px;
  margin-bottom: 1rem;
  margin-top: 0.5rem;
  padding-left: 0.6rem;
}

.crud-modal-form textarea {
  min-height: 70px;
  height: auto;
}

.crud-modal-form__label {
  color: #000;
  display: block;
  text-align: left;
  font-weight: 600;
}

.crud-button {
  background-color: #000;
  min-height: 1rem;
  color: #fff;
  border-radius: 3px;
  padding: 0.6rem 1rem;
  border: none;
  font-size: 1rem;
  font-family: 'Poppins';
  cursor: pointer;
}

.crud-button--circular {
  border-radius: 50%;
  padding: 0;
  width: 2rem;
  font-family: 'Poppins';
  line-height: 2rem;
}

.crud-button--primary {
  background-color: #2185d0;
  font-family: 'Poppins';
}

.crud-button--primary:hover {
  background-color: #1a6fb1;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  font-family: 'Poppins';
}

.crud-button--positive {
  background-color: #50577A;
  font-family: 'Poppins';
}

.crud-button--positive:hover {
  background-color: #494d5f;
  font-family: 'Poppins';
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

.crud-button--negative {
  background-color: #f51010;
  font-family: 'Poppins';
}

.crud-button--negative:hover {
  background-color: #b80f0f;
  font-family: 'Poppins';
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

.crud-table {
  background-color: transparent;
  border: 1px solid rgba(52, 145, 238, 0.15);
  border-collapse: collapse;
  -webkit-box-shadow: 2px 2px 5px 0 #c7c7c7;
          box-shadow: 2px 2px 5px 0 #c7c7c7;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  text-align: center;
}
.crud-table__row {
}
.crud-table__header-cell {
  cursor: pointer;
  background: rgba(153, 158, 162, 0.05);
  padding: 0.5rem 1rem;
  color: rgba(0, 0, 0, 0.85);
  border-left: 1px solid rgba(34, 36, 38, 0.1);
  border-bottom: 1px solid rgba(34, 36, 38, 0.1);
}

.crud-table__header {
}

.crud-table__body {
  background-color: #fff;
}

.crud-table__cell {
  background-color: white;
  padding: 0.5rem 1rem;
  border-left: 1px solid rgba(34, 36, 38, 0.1);
  border-bottom: 1px solid rgba(4, 21, 37, 0.1);
}

.crud-table__cell-label {
  display: none;
}

.crud-table__caption {
  background-color: white;
  padding: 0.8rem;
  margin: 1rem 0;
  -webkit-box-shadow: 2px 2px 10px 0 #c7c7c7;
          box-shadow: 2px 2px 10px 0 #c7c7c7;
  font-size: 1.5rem;
  text-align: center;
  font-weight: 600;
  border-radius: 6px;
}

.crud-table-pagination {
  display: block;
  margin-top: 1rem;
  margin: auto;
}

.crud-table-pagination::before {
  content: "";
  clear: both;
  display: block;
  margin-top: 2rem;
}

.crud-table-pagination__link,
.crud-table-pagination__previous,
.crud-table-pagination__next {
  color: #000;
  float: left;
  padding: 8px 16px;
  text-decoration: none;
  -webkit-transition: background-color 0.3s;
  -o-transition: background-color 0.3s;
  transition: background-color 0.3s;
  border: 1px solid #ddd;
  background-color: #fff;
  cursor: pointer;
  -webkit-box-shadow: 2px 2px 5px 0 #c7c7c7;
          box-shadow: 2px 2px 5px 0 #c7c7c7;
  border-radius: 2px;
}

.crud-table-pagination__link--active {
  background-color: rgb(32, 132, 228);
  color: #fff;
  border: 1px solid rgb(26, 112, 197);
}

.crud-table-pagination__link:hover,
.crud-table-pagination__previous:hover,
.crud-table-pagination__next:hover {
  background-color: #3bbf5c;
  color: #fff;
  border-color: #28a93e;
}

.crud-table-query-builder {
  background-color: #fff;
  padding: 1rem;
  border-radius: 0.2rem;
  margin-bottom: 1rem;
  -webkit-box-shadow: 2px 2px 10px 0 #c7c7c7;
          box-shadow: 2px 2px 10px 0 #c7c7c7;
}

.crud-table-query-builder select,
.crud-table-query-builder input {
  border-radius: 5px;
  height: 30px;
  border: 1px solid #ccc;
  min-width: 150px;
  margin-bottom: 1rem;
  margin-top: 0.5rem;
  padding-left: 0.6rem;
}

.crud-label {
  display: inline-block;
  border: 1px solid #e5e5e5;
  padding: 0.48rem 1rem;
  border-radius: 0.2rem;
  background-color: #f9fafb;
  font-size: 1.1rem;
  margin-right: 0.5rem;
  margin-bottom: 0.7rem;
}

@media screen and (max-width: 600px) {
  .crud-table {
    display: block;
    min-width: 85vw;
    margin-top: 1rem;
    border-radius: 3px;
    overflow: hidden;
    margin-bottom: 2rem;
  }

  .crud-table__row,
  .crud-table__caption,
  .crud-table__header,
  .crud-table__body,
  .crud-table__cell {
    display: block;
    width: 100%;
  }

  .crud-table__caption {
    margin: 1rem 0;
    border-bottom: 1px solid #e6e6e6;
    max-width: 100%;
    display: block;
    width: auto;
  }

  .crud-table__header,
  .crud-table__header-cell {
    display: none;
  }

  .crud-table__cell {
    border-bottom: 0;
    border-left: 0;
  }

  .crud-table__cell:last-child {
    padding-bottom: 2rem;
  }

  .crud-table__row {
    border-bottom: 1px solid #e6e6e6;
  }

  .crud-table__row:not(:last-child) {
    margin-bottom: 2rem;
  }

  .crud-table__cell-label {
    display: block;
    font-size: 1.2rem;
    font-weight: bold;
    border-bottom: 1px dashed #e6e6e6;
    padding: 0.5rem;
    margin-bottom: 0.5rem;
    background: rgba(184, 187, 189, 0.05);
  }

  .crud-table-query-builder .crud-button--add {
    margin: 0 auto 2rem auto;
    display: block;
    border-radius: 50%;
    font-family: 'Poppins';
    line-height: 3rem;
    width: 3rem;
    padding: 0;
    font-size: 1.6rem;
  }

  .crud-table-query-builder select,
  .crud-table-query-builder input {
    width: 100%;
    margin-bottom: 0;
  }
}

.topbar{
  width: 100%;
  height: 80px;
  background-color: #474E68;
  position: sticky;
  top: 0;
  z-index: 999;
}

.topbarWrapper{
  height: 100%;
  padding: 0px 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-left: 4%;
  margin-right: 4%;
}

.logo-text{
  font-weight: bold;
  font-size: 30px;
  color: rgb(255, 255, 255);
  cursor: pointer;
}

.topRight{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 1px;
}

.topbarIconContainer{
  position: relative;
  cursor: pointer;
  margin-right: 10px;
  color: rgb(255, 255, 255);
}

.topIconBadge{
  width: 15px;
  height: 15px;
  position: absolute;
  top: -5px;
  right: 0px;
  background-color: red;
  color: white;
  border-radius: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 10px;
}

.topAvatar{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  margin-right: 10px;
}

.signout-button{
display: inline-block;
border-width: 0px;
width: 90px;
padding: 8px;
margin: 10px;
border-radius: 5px;
color: white;
background-color:  #5F6F94;
font-family: Poppins;
-webkit-transition-duration: 0.4s; /* Safari */
-o-transition-duration: 0.4s;
   transition-duration: 0.4s;
}

.signout-button:hover {
  -webkit-box-shadow: 0 12px 16px 0 rgba(0,0,0,0.13), 0 17px 50px 0 rgba(0,0,0,0.10);
          box-shadow: 0 12px 16px 0 rgba(0,0,0,0.13), 0 17px 50px 0 rgba(0,0,0,0.10);
}

.footer{
  margin-top: 1rem;
  padding: 1rem;
  background-color: #474E68;
  text-align: center;
  height: 56px;
  color: white;
  position: sticky;
  top: 100%;
}

.footer, p{
  margin-top: 20px;
}


